.accord{margin-bottom:2.4rem}.title{display:flex;flex-direction:row;align-items:center;justify-content:space-between;padding:.6rem 1.2rem;margin-bottom:.8rem;width:100%;cursor:pointer;border-bottom:1px solid var(--colorSecondary)}.title:focus,.title:hover,.title:active{background-color:var(--colorSecondary)}.title:after{content:"+";font-size:2rem;color:inherit;float:right}.list{display:flex;flex-direction:column;gap:.8rem;padding:0 1.2rem;max-height:0;overflow:hidden;visibility:hidden;transition:all .3s}.list a,.list a:link,.list a:visited{color:inherit}.list a:focus,.list a:hover,.list a:active{text-decoration:underline}.active .title{background-color:var(--colorSecondary)}.active .title:after{content:"−"}.active .list{visibility:visible;max-height:32rem}