@use "../../../../../../styles/variables/static";

.sub {
  display: grid;
  grid-template-columns: repeat(5, minmax(0, 1fr));
  justify-items: center;
  align-items: center;
  column-gap: 3.2rem;

  border-bottom: 1px solid var(--colorSecondary);
  padding: 2.4rem 0 0.8rem;

  @media only screen and (max-width: static.$bpLarge) {
    grid-template-columns: repeat(4, minmax(0, 1fr));
    column-gap: 0rem;
  }

  @media only screen and (max-width: static.$bpMedium) {
    grid-template-columns: repeat(1, 1fr);
  }
}

.img {
  height: 32rem;
  width: 100%;
  grid-column: span 1;
  grid-row: 1 / span 3;

  position: relative;
  z-index: 5;

  display: flex;
  flex-direction: column;
  align-items: flex-end;

  @media only screen and (max-width: static.$bpLarge) {
    grid-template-columns: repeat(4, minmax(0, 1fr));
    column-gap: 0rem;
  }

  @media only screen and (max-width: static.$bpMedium) {
    display: none;
  }
}

.img {
  img {
    height: 100%;
    // width: 100%;

    object-fit: cover;
    object-position: bottom left;
  }
}

.title {
  grid-column: span 1;
  grid-row: span 1;
  text-align: right;

  justify-self: end;
  align-self: start;
  margin: 0 !important;
  margin-bottom: 0.4rem !important;

  font-weight: 800;
  font-size: 5.4rem;

  line-height: 0.9;
  letter-spacing: -0.4rem;

  position: relative;
  z-index: 10;

  @media only screen and (max-width: static.$bpMedium) {
    grid-row: auto;
    justify-self: center;

    font-size: 3rem;
    font-weight: 500;
    letter-spacing: -0.2rem;
    margin-bottom: 3.2rem !important;
  }
}

.sub {
  > a {
    grid-column: 1 / span 4;
    grid-row: 3 / span 2;

    margin-top: 3.2rem;
    margin-bottom: 0.4rem;

    @media only screen and (max-width: static.$bpLarge) {
      grid-column: 1 / span 3;
    }

    @media only screen and (max-width: static.$bpMedium) {
      grid-row: auto;
    }
  }
}

.container {
  grid-template-columns: repeat(4, 1fr);

  grid-row: 1 / span 2;
  grid-column: 1 / span 4;

  @media only screen and (max-width: static.$bpLarge) {
    grid-column: 1 / span 3;
  }

  @media only screen and (max-width: static.$bpMedium) {
    grid-row: auto;
  }

  @media only screen and (max-width: static.$bpSmallest) {
    grid-template-columns: repeat(2, 1fr);
  }
}
